body {
  background-color: white;
}

.center {
  display: grid;
  place-items: center;
  min-height: 100vh;
  margin: 0;
}

.flex {
  display: flex;
  gap: 2rem;
}

html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

.flag {
  width: 32rem;
  height: 20rem;
  background-color: #006aa7;
  position: relative;
}
.flag::before {
  content: "";
  background-color: #fecc00;
  height: 20%;
  width: 100%;
  position: absolute;
  top: 40%;
}
.flag::after {
  content: "";
  background-color: #fecc00;
  height: 100%;
  width: 12.5%;
  position: absolute;
  left: 31.25%;
  top: 0;
}